.page-header {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #172131;
}

.page-header__logo {
  color: #ffffff;
  display: block;

  &:hover {
    color: $lipstick;
  }

  &:active {
    color: #ffffff;
    opacity: 0.3;
  }
}

.page-header__image {
  fill: currentColor;
  display: block;

  &--tablet,
  &--desktop {
    display: none;
  }
}

@media (min-resolution: $retina-dppx) {
  .page-header {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    background-image: url("../img/bg-back-tablet@1x.jpg");
  }

  .page-header__logo {
    position: relative;
    top: -5px;
  }

  .page-header__image {
    &--tablet {
      display: block;
    }

    &--mobile,
    &--desktop {
      display: none;
    }
  }

  @media (min-resolution: $retina-dppx) {
    .page-header {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }
  }
}

@media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
  .page-header__top {
    min-height: 110px;
  }
}

@media (max-width: $tablet-width-only) {
  .page-header__nav {
    display: none;
  }

  .page-header__toggle {
    position: relative;
    width: 50px;
    height: 24px;
    background-color: transparent;
    border: none;
    padding: 0;

    &::before {
      content: "";
      width: 50px;
      height: 24px;
      display: block;
      background-image: url("../img/icon-menu-burger.svg");
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .page-header__container--active {
    background-color: #172131;

    .page-header__nav {
      display: block;
    }

    .page-header__toggle {
      &::before {
        background-image: url("../img/icon-menu-cross.svg");
        background-position: center right;
      }
    }
  }

  .page-header__top {
    padding: 23px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}


@media (min-width: $desktop-width) {
  .page-header {
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }

  .page-header__outer {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .page-header__toggle {
    display: none;
  }

  .page-header__container {
    width: 940px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 107px;
  }

  .page-header__image {
    &--desktop {
      display: block;
    }

    &--mobile,
    &--tablet {
      display: none;
    }
  }

  @media (min-resolution: $retina-dppx) {
    .page-header {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }
  }
}
